.memaa-options {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
        display: inline-block;
        margin: .25em;

        a {
            display: inline-block;
            color: inherit;
            text-decoration: none;
            padding: .1em .5em;
            border: 1px solid $border-color;
            border-radius: .5em;
        }
    }
}

.memaa-true {
    border-color: $green !important;
}

.memaa-false {
    border-color: $red !important;
    animation: blink ease .4s 2;
}

.memaa {
    display: flex;
    min-height: max-content;
}

.memaa-right {
    display: flex;
    align-items: center;
    padding: 0 .3em;
    margin-left: auto;
}

.memaa-fade-in {
    animation: fadeIn ease .8s;
}

.memaa-hshake {
    animation: hshake linear 1s infinite;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
        translate: -.5em;
    }

    100% {
        opacity: 1;
    }
}

@keyframes blink {
    0% {
        opacity: 1;
    }

    50% {
        opacity: .4;
    }

    100% {
        opacity: 1;
    }
}

@keyframes hshake {
    25% {
        translate: -0.2em;
    }

    75% {
        translate: 0.2em;
    }
}

@media (any-hover: hover) {
    .memaa-options>li>a:hover {
        border-color: $link-hover-color;
    }

    .memaa-right:hover {
        cursor: pointer;
        background-color: $gray-100;
    }
}