$gray-000: #f8f9fa;
$gray-100: #f1f3f5;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #868e96;
$gray-700: #495057;
$gray-800: #343a40;
$gray-900: #212529;

$red: #fa5252;
$pink: #e64980;
$grape: #be4bdb;
$purple: #7950f2;
$indigo: #4c6ef5;
$blue: #228be6;
$cyan: #15aabf;
$teal: #12b886;
$green: #40c057;
$yellow: #fab005;
$orange: #fd7e14;

$blue-300: #74c0fc;
$blue-400: #4dabf7;
$yellow-100: #fff3bf;

$body-font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$body-font-size: 18px;
$body-line-height: 1.5;
$body-color: $gray-700;
$body-bg: white;

$text-font: Times, "Times New Roman", "Free Serif", "Noto Serif", TimesNewRomanPSMT, "Droid Serif";

$link-color: $blue;
$link-hover-color: #1c7ed6;

$heading-color: $gray-900;

$border-color: $gray-300;
$border-radius: .25rem;

$code-font: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
$code-color: $grape;
$code-bg: #fbf1c7;

$spacer: 1rem;
$spacer-2: $spacer * 1.5;
$spacer-3: $spacer * 3;

$sm-width: 35.5rem;
$md-width: 48rem;